import { ErrorMessage, Form, Formik } from "formik";
import React from "react";
import MyTextInput from "../../app/common/form/MyTextInput";
import { Button, Header } from "semantic-ui-react";
import { observer } from "mobx-react-lite";
import { useStore } from "../../app/stores/store";
import * as Yup from 'yup';
import ValidationErrors from "../errors/ValidationErrors";

export default observer( function RegisterForm() {
    const {userStore} = useStore();
    return (
        <Formik
            initialValues={{displayName: '', username: '', email: '', password: '', error: null}}
            onSubmit={(values, {setErrors}) => userStore.register(values).catch(error => 
                setErrors({error: error}))}
            validationSchema={Yup.object({
                displayName: Yup.string()
                .required("   ")
                .min(3, '    2 ')
                .max(50, '     50 !'),
                username: Yup.string()
                .required("    ")
                .min(3, '    2 ')
                .max(50, '      50 !'),
                email: Yup.string()
                .required("   ")
                .email(),
                password: Yup.string()
                .required("    ")
                .min(4, '   4  12 !')
                .max(12, '   4  12 !'),
            })}
        > 

            {({handleSubmit, isSubmitting, errors, isValid, dirty}) => (
                <Form className='ui form error' onSubmit={handleSubmit} autoComplete='off'>
                    <Header as='h2' content='' color='teal' textAlign='center' />
                    <MyTextInput name='displayName' placeholder='' />
                    <MyTextInput name='username' placeholder='' />
                    <MyTextInput name='email' placeholder=' ' />
                    <MyTextInput name='password' placeholder='' type='password'/>
                    <ErrorMessage 
                        name='error' render={() => 
                            <ValidationErrors errors={errors.error}/>}
                    />
                    <Button disabled={!isValid || !dirty || isSubmitting} 
                        loading={isSubmitting} positive content='' type='submit' fluid/>
                </Form>
            )}
        </Formik>
    )
})