import React, { useEffect, useState } from "react";
import { Button, Header, Segment } from "semantic-ui-react";
import { useStore } from "../../../app/stores/store";
import { observer } from "mobx-react-lite";
import { Link, useHistory, useParams } from "react-router-dom";
import LoadingComponent from "../../../app/layout/LoadingComponent";
import {v4 as uuid} from "uuid";
import { Formik, Form } from "formik";
import * as Yup from 'yup';
import MyTextInput from "../../../app/common/form/MyTextInput";
import MyTextArea from "../../../app/common/form/MyTextArea";
import { categoryOptions } from "../../../app/common/options/categoryOptions";
import MySelectInput from "../../../app/common/form/MySelectInput";
import MyDateInput from "../../../app/common/form/MyDateInput";
import { ActivityFormValues } from "../../../app/models/activity";

export default observer (function ActivityForm() {
    const history = useHistory();
    const {activityStore} = useStore();
    const {createActivity, updateActivity, loadActivity, loadingInitial} = activityStore;
    const {id} = useParams<{id: string}>();

    const[activity, setActivity] = useState<ActivityFormValues>(new ActivityFormValues());

    const activityValidationSchema = Yup.object({
        title: Yup.string()
            .required('    ')
            .min(3, '    2 ')
            .max(50, '     50 !'),
        category: Yup.string()
            .required('    ')
            .min(3, '    2 ')
            .max(30, '     30 !'),
        description: Yup.string()
            .required('    ')
            .min(10, '    10 ')
            .max(500, '     500 !'),
        date: Yup.date()
            .required('    '),
        city: Yup.string()
            .required('    ')
            .min(2, '    1 ')
            .max(50, '     50 !'),
        venue: Yup.string()
            .required('     ')
            .min(2, '     1 ')
            .max(100, '      100 !')
    });

    useEffect(() => {
        if (id) loadActivity(id).then(activity => setActivity(new ActivityFormValues(activity)))
    }, [id, loadActivity]);

    function handleFormSubmit(activity: ActivityFormValues) {
        if (!activity.id) {
            let newActivity = {
                ...activity,
                id: uuid()
            };
            createActivity(newActivity).then(() => history.push(`/activities/${newActivity.id}`))
        } else {
            updateActivity(activity).then(() => history.push(`/activities/${activity.id}`))
        }
    }

    if (loadingInitial) return <LoadingComponent content='Loading activity...'/>

    return (
        <Segment clearing>
            <Header content='  ' sub color='teal'/>
            <Formik 
                    validationSchema={activityValidationSchema}
                    enableReinitialize 
                    initialValues={activity} 
                    onSubmit={values => handleFormSubmit(values)}>
                {({handleSubmit, isValid, isSubmitting, dirty}) => (
                    <Form className='ui form' onSubmit={handleSubmit} autoComplete='off'>
                        <MyTextInput name='title' placeholder=''/>
                        <MyTextArea rows={3} placeholder='' name='description'/>
                        <MySelectInput options={categoryOptions} placeholder='' name='category'/>
                        <MyDateInput 
                            placeholderText=""
                            name='date' 
                            showTimeSelect
                            timeCaption=''
                            dateFormat='MMMM d, yyyy h:mm aa'
                        />
                        <Header content='   ' sub color='teal'/>
                        <MyTextInput placeholder='' name='city'/>
                        <MyTextInput placeholder='' name='venue'/>
                        <Button 
                            disabled={isSubmitting || !dirty || !isValid}
                            loading={isSubmitting} floated='right' 
                            positive type='submit' content=''/>
                        <Button as={Link} to='/activities' floated='right' type='button'content=''/>
                    </Form>
                )}
            </Formik>
        </Segment>
    )
}); 